$gutter: 1rem;

@import 'node_modules/primeflex/primeflex.scss';

@import 'prismjs/themes/prism-coy.css';
@import './AppDemo.scss';

/*default version*/
@font-face {
	font-family: 'ITCAvantGardePro';
	src: url('./../src/assets/font/ITCAvantGardePro-Bold_1.otf'); 
	src:     
		local('ITCAvantGardePro'),
		local('ITCAvantGardePro'),
		url('./../src/assets/font/ITCAvantGardePro-Bold_1.otf') 
		format('opentype');
}
/*bold version*/
@font-face {
	font-family: 'ITCAvantGardePro';
	src: url('./../src/assets/font/ITCAvantGardePro-Bold_1.otf'); 
	src: 
        local('ITCAvantGardePro'),
        local('ITCAvantGardePro'),
		url('./../src/assets/font/ITCAvantGardePro-Bold_1.otf') 
		format('opentype');
	font-weight: bold;
}
.ck {
    font-family: 'ITCAvantGardePro', sans-serif;
}
.ck-bold { font-weight: bold; }
.ck-black { color: #636466; }
.ck-purple { color: #8A05BE; }


/* MGE default version */
@font-face {
	font-family: 'Overpass';
	src: url('./../src/assets/font/Overpass-VariableFont_wght.ttf'); 
	src:     
		local('Overpass'),
		local('Overpass'),
		url('./../src/assets/font/Overpass-VariableFont_wght.ttf') 
		format('truetype');
}
/*MGE bold version */
@font-face {
	font-family: 'OverpassItalic';
	src: url('./../src/assets/font/Overpass-Italic-VariableFont_wght.ttf'); 
	src: 
        local('OverpassItalic'),
        local('OverpassItalic'),
		url('./../src/assets/font/Overpass-Italic-VariableFont_wght.ttf') 
		format('opentype');
}
.mge {
    font-family: 'Overpass', sans-serif;
}
.mge-bold { font-weight: bold; }
.mge-black { color: #636466; }
.mge-green { color: #54873C; }
.mge-lightGreen { color: #70B450; }
.mge-darkGreen { color: #385A28; }

@import '~antd/dist/antd.css';